body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9f9f9;
  color: #333;
}

.app-container {
  padding: 1rem;
}

.app-header {
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1rem;
  color: #e63946;
}

.msr-title {
  color: #e63946;
}

.search-bar {
  display: block;
  margin: 0 auto 1rem auto;
  padding: 0.5rem;
  width: 80%;
  max-width: 400px;
  border: 1px solid #ccc;
  border-radius: 8px;
}

.table-container {
  overflow-x: auto;
  background: white;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
}

.leaderboard-table {
  width: 100%;
  min-width: 1000px;
  border-collapse: collapse;
}

.leaderboard-table th,
.leaderboard-table td {
  padding: 0.5rem;
  text-align: center;
  border-bottom: 1px solid #eee;
}

.leaderboard-table thead {
  background-color: #f1f1f1;
  position: sticky;
  top: 0;
  z-index: 1;
}

.highlight-msr {
  background-color: #0077b6;
  color: white;
  font-weight: bold;
  border-radius: 4px;
}

th.sortable:hover {
  cursor: pointer;
  background-color: #e0e0e0;
}














